// @flow

import * as React from 'react';
import { connect } from 'react-redux';

import { Title } from './styled';
import * as appActions from './actions';

type Props = {
  requestSessions: Function,
  sessions: Array<any>,
};

class SessionsReview extends React.Component<Props> {
  socket: Socket;

  constructor(props) {
    super(props);
  }
  componentDidMount() {
    this.socket = new WebSocket('ws://localhost:8080');
    this.socket.addEventListener('open', event => {
      log('Sockets opened in client', event);
      this.socket.send('Hello Server!');
      this.socket.onmessage = this.socketMsg;
    });
    this.props.requestSessions();
  }

  componentWillUnmount() {
    this.socket.close();
  }

  socketMsg = event => {
    log('Message from server ', event.data);
    this.props.requestSessions();
  };

  render = () => (
    <section>
      <Title>SessionsReview</Title>
      <ul>
        {this.props.sessions
          ? this.props.sessions.list.map((session, idx) => (
              <li key={idx}>
                {session.id}
                <br />
                {session.step1 ? <span>step1</span> : null}
                {session.step2 ? <span>step2</span> : null}
                {session.step3 ? <span>step3</span> : null}
              </li>
            ))
          : null}
      </ul>
    </section>
  );
}

const mapStateToProps = ({ sessions }) => ({
  sessions,
});

export default connect(mapStateToProps, {
  ...appActions,
})(SessionsReview);
