// @flow

import * as React from 'react';

import { Option, OptionWrapper } from './styled';
import Toogle from 'components/Toogle';

type ToogleOptionProps = {
  label: string,
  on: boolean,
  onClick: Function,
};

export const ToogleOption = ({ label, on, onClick }: ToogleOptionProps) => (
  <Option>
    <OptionWrapper>
      {label}
      <Toogle on={on} onClick={onClick} />
    </OptionWrapper>
  </Option>
);

type OptionsGroupOptionProps = {
  label: string,
  options: Array<any>,
  name: string,
  selected: string,
  onChange: Function,
};

export const OptionsGroupOption = ({
  label,
  options,
  name,
  selected,
  onChange,
}: OptionsGroupOptionProps) => (
  <Option>
    <OptionWrapper>
      <div>{label}</div>
      <div>
        {options.map((value, idx) => (
          <React.Fragment key={idx}>
            <input
              id={value}
              type="radio"
              name={name}
              value={value}
              checked={selected === value}
              onChange={onChange}
            />
            <label htmlFor={value}>{value}</label>
          </React.Fragment>
        ))}
      </div>
    </OptionWrapper>
  </Option>
);

type RangeOptionProps = {
  label: string,
  value: string,
  min: string,
  max: string,
  onChange: Function,
};

export const RangeOption = ({
  label,
  value,
  min,
  max,
  onChange,
}: RangeOptionProps) => (
  <Option>
    <OptionWrapper>
      <div>{label}</div>
      <input
        type="range"
        min={min}
        max={max}
        value={value}
        onChange={onChange}
        className="slider"
      />
    </OptionWrapper>
  </Option>
);
